<template>
    <li>
        <div>
            <a :href="emailLink">{{ email }}</a>
        </div>
        <p>{{ message }}</p>
    </li>
</template>

<script>
export default {
    props:['email','message'],
    computed:{
        emailLink(){
            return "https://mail.google.com/mail/u/0/#inbox" + this.email;
        }
    }
}
</script>

<style scoped>
li {
  margin: 1rem 0;
  border: 1px solid #ccc;
  border-radius: 2rem;
  padding-top: 3rem;
  padding-bottom: 3rem;
  padding-left: 4rem;
  padding-right: 4rem;
  margin-top: -15px;
}

a {
  color: #ff8000;
  text-decoration: none;
  font-weight: bold;
}

a:hover,
a:active {
  color: #2f3136;
}

p {
  margin: 0.5rem 0 0 0;
}
</style>