<template>
    <div class="card">
    <h2>查詢浪浪</h2>
    <p>(Pet Query)</p>
    <div class="d-flex justify-content-center">
    <div class="form-check form-check-inline">
        <input type="checkbox" id="男生" checked @change="setFilter" class="form-check-input">
        <label for="男生" class="form-check-label">男生</label>
    </div>
    <div class="form-check form-check-inline">
        <input type="checkbox" id="女生" checked @change="setFilter" class="form-check-input">
        <label for="女生" class="form-check-label">女生</label>
    </div>
    <div class="form-check form-check-inline">
        <input type="checkbox" id="狗狗" checked @change="setFilter" class="form-check-input">
        <label for="狗狗" class="form-check-label">狗狗</label>
    </div>
    <div class="form-check form-check-inline">
        <input type="checkbox" id="貓貓" checked @change="setFilter" class="form-check-input">
        <label for="貓貓" class="form-check-label">貓貓</label>
    </div>
    <div class="form-check form-check-inline">
        <input type="checkbox" id="未結紮" checked @change="setFilter" class="form-check-input">
        <label for="未結紮" class="form-check-label">未結紮</label>
    </div>
    <div class="form-check form-check-inline">
        <input type="checkbox" id="結紮" checked @change="setFilter" class="form-check-input">
        <label for="結紮" class="form-check-label">結紮</label>
    </div>
</div>
    </div>
</template>

<script>
export default {
    emits:['change-filter'],
    data(){
        return{
            filters:{
                男生:true,
                女生:true,
                未結紮:true,
                結紮:true,
                狗狗:true,
                貓貓:true,
            }
        }
    },
    methods:{
        setFilter(event){
            const inputId = event.target.id;
            //這裡是管理checkbox的 checked
            const isActive = event.target.checked;
            //更新filter，...選取全部
            const updatedFilters ={
                ...this.filters,
                //動態設置屬性(?)，顯示上面不同id屬性綁定isActive
                [inputId]:isActive
            };
            this.filters = updatedFilters;
            this.$emit('change-filter',updatedFilters)
        }
    }
    
}
</script>

<style scoped>
h2 {
text-decoration: none;
padding-top:1.5rem;
color: #25272c;
margin-top: 1rem;
text-align: center;
font-weight:bolder;
font-size: 2.5rem;
}

p{
    text-decoration: none;
color: #25272c;
margin-top: -5px ;
text-align: center;
font-weight:bolder;
font-size: 1.2rem;
letter-spacing: 0.1rem;
}

.form-check{
    font-size:100%;  
    font-size: 18px;
}
.form-check-input{
    width: 1.25rem;
    height: 1.25rem;
    transition: all .3s linear;
    border: none !important;
    
}
.form-check-input:checked{
    background-color: #2f3136 !important;
}

.card{
    border-radius: 12px;
    box-shadow:4px 3px 16px -7px #454545;
    padding: 1rem;
    margin: 2rem auto;
    max-width: 35rem;
    border: none;
    background-color:#ffd46e;
}

.form-check-label{
    color: #2f3136;
}

</style>

