<template>
    <section>
        <div class="base-card">
        <h2>基本資料</h2>
        <p>(Basic Information)</p>
    </div>
        <base-card>
        <!--這是下層的emit傳上來的-->
        <pets-from @save-data="saveData"></pets-from>
    </base-card>
    </section>
</template>

<script>
    import PetsFrom from '../../components/pets/PetsFrom.vue';

export default{
    components:{
        PetsFrom
    },
    methods:{
    //這個data是下層的formData裡所有資料
        saveData(data){
    //這裡是從action和mutation裡使用methods
            this.$store.dispatch('pets/registerPet',data)
            this.$router.replace('/pets')
    //使用replace可引導我們離開頁面，但無法在跳轉回去
        },
    }
}
</script>

