export default {
  async contactPet(context, payload) {
    const newRequest = {
      userEmail: payload.email.val,
      message: payload.message.val,
    };
    const response = await fetch(
      `https://vue-http-demo-3843c-default-rtdb.firebaseio.com/requests/${payload.petId}.json`,
      {
        //這裡設置POST是不想覆蓋現有的資料
        method: "POST",
        body: JSON.stringify(newRequest),
      }
    );

    const responseData = await response.json();

    if (!response.ok) {
      const error = new Error(
        responseData.message || "錯誤的訊息，資料上傳失敗"
      );
      throw error;
    }
    //從firebase回傳的數據中，新增newRequest.id是responseData裡的name，即可自動生成id
    newRequest.id = responseData.name.val;
    newRequest.petId = payload.petId;

    context.commit("addRequest", newRequest);
  },

  async fetchRequests(context) {
    const petId = context.rootGetters.userId;
    const response = await fetch(
      `https://vue-http-demo-3843c-default-rtdb.firebaseio.com/requests/${petId}.json`
    );
    const responseData = await response.json();

    if (!response.ok) {
      const error = new Error(
        responseData.message || "錯誤的訊息，資料請求失敗"
      );
      throw error;
    }
    //將要放進ContactPets資料彙整起來
    const requests = [];

    for (const key in responseData) {
      const request = {
        id: key,
        petId: petId,
        userEmail: responseData[key].userEmail,
        message: responseData[key].message,
      };
      requests.push(request);
    }
    context.commit("setRequest", requests);
  },
};
