<template>
    <div>
    <!--兩個!!是因為第一個!代表show的布林值，第二個!才為字串符的布林值-->
    <base-dialog :show="!!error" title="錯誤回應" @close="handleError">
    <!--這裡來自最下方catch error-->
    <p>{{error}}</p>
</base-dialog>
<section>
<!--篩選寵物器-->
<pets-filter @change-filter="setFilters"></pets-filter>
</section>
<section >
<!--全部寵物訊息-->
<base-card>
<div class="controls">
    <base-button mode="outline" @click="loadPets(true)">刷新頁面</base-button>
    <!--這邊寫link等於link=true-->
    <base-button v-if="!isPet && !isLoading" link to="/register">登記浪浪</base-button>
</div>
<!--設定加載畫面-->
<div v-if="isLoading">
    <base-spinner></base-spinner>
</div>
<ul v-else-if="hasPets" class="center">
    <pets-item 
    v-for="pet in filterPets" 
    :key="pet.id" 
    :id="pet.id" 
    :name="pet.name"
    :born="pet.born"
    :states="pet.states"
    :pet="pet"
    ></pets-item>
</ul>
<h3 v-else>找不到相關浪浪</h3>
</base-card>
</section>
</div>
</template>

<script>
import PetsItem from '../../components/pets/PetsItem.vue';
import BaseDialog from '../../components/UI/BaseDialog.vue';
export default {
    components:{
    PetsItem,
    BaseDialog
},
    data(){
        return{
            isLoading:false,
            error:null,
            activeFilters:{
                男生:true ,
                女生:true,
                未結紮:true,
                結紮:true,
                狗狗:true,
                貓貓:true,
            }
        }
    },
    computed:{
        isPet(){
            return this.$store.getters['pets/isPet']
        },
        filterPets(){
           const pets = this.$store.getters['pets/pets'];
           return pets.filter(pet =>{
            if(this.activeFilters.男生 && pet.states.includes("男生")){
                return true;
            }
            if(this.activeFilters.女生 && pet.states.includes("女生")){
                return true;
            }
            if(this.activeFilters.未結紮 && pet.states.includes("未結紮")){
                return true;
            }
            if(this.activeFilters.結紮 && pet.states.includes("結紮")){
                return true;
            }
            if(this.activeFilters.狗狗 && pet.states.includes("狗狗")){
                return true;
            }
            if(this.activeFilters.貓貓 && pet.states.includes("貓貓")){
                return true;
            }
            return false;
           })
        },
        hasPets(){
            //isLoading=false就會顯示pets列表
            return !this.isLoading && this.$store.getters['pets/hasPets']
        }
    },
    created() {
        this.loadPets();
    },
    methods:{
        setFilters(updatedFilters){
            this.activeFilters = updatedFilters;
        },
        async loadPets(refresh = false){
            this.isLoading = true;
            //dispatch可將某些內容返回到調用前的組件
            //這裡是指firebase資料
            try{
                await this.$store.dispatch('pets/loadPets', {
            forceRefresh: refresh
            })
            //當出現錯誤時顯示"網頁錯誤請修改"
            }catch(error){
                this.error = error.message ||"網頁錯誤請修改"
            }
            this.isLoading = false;
        },
        handleError(){
            this.error = null
        }
    },
}
</script>

<style scoped>
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.controls {
  display: flex;
  justify-content: space-between;
  margin-top: 1.2rem;
}

</style>