<template>
  <div>
    <button v-if="!link" :class="mode">
        <slot></slot>
    </button>
    <router-link v-else :to="to" :class="mode">
        <slot></slot>
    </router-link>
  </div>
</template>

<script>
export default {
    //設定class為mode
    props:{
        mode:{
        type:String,
        required:false,
        default:null,
        },
        link:{
            type:Boolean,
            required:false,
            default:false,
        },
    //將上層to功能沿用
        to:{
            type:String,
            required:false,
            default:'/',
        }
    }
}
</script>
<style scoped>
button,
a {
  text-decoration: none;
  padding: 0.75rem 1.5rem;
  font: inherit;
  background-color: #2f3136;
  border: 1px solid #2f3136;
  color: white;
  cursor: pointer;
  border-radius: 30px;
  margin-right: 0.5rem;
  display: inline-block;
}

a:hover,
a:active,
button:hover,
button:active {
  background-color: #f09c00;
  border-color: #f09c00;
  color: white;
}

.flat {
  background-color: transparent;
  color: #f09c00;
  border: none;
}

.outline{
  background-color: transparent;
  border-color: #f09c00;
  color: #f09c00;
}

.flat:hover,
.flat:active,
.outline:hover,
.outline:active {
  background-color: #ffd87e;
  color: #2f3136;
}
</style>