import { createRouter, createWebHistory } from "vue-router";
import PetsDetail from "./page/pets/PetsDetail.vue";
import PetsList from "./page/pets/PetsList.vue";
import PetsRegister from "./page/pets/PetsRegister.vue";
import ContactPets from "./page/requests/ContactPets.vue";
import RequestsReceived from "./page/requests/RequestsReceived.vue";

const router = createRouter({
  history: createWebHistory(),
  routes: [
    { path: "/", redirect: "/pets" },
    { path: "/pets", component: PetsList },
    {
      path: "/pets/:id",
      component: PetsDetail,
      props: true,
      children: [{ path: "contact", component: ContactPets }], // pets/:id/contact
    },
    { path: "/register", component: PetsRegister },

    { path: "/requests", component: RequestsReceived },
    { path: "/:notFound(.*)", component: PetsList },
  ],
});

export default router;
