import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store/index.js";
import BaseCard from "./components/UI/BaseCard.vue";
import BaseButton from "./components/UI/BaseButton.vue";
import BaseBadge from "./components/UI/BaseBadge.vue";
import PetsFilter from "./components/pets/PetsFilter.vue";
import BaseSpinner from "./components/UI/BaseSpinner.vue";
import BaseDialog from "./components/UI/BaseDialog.vue";

//bootstrap
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap/dist/js/bootstrap.js";
//字體安裝
import "taipei-sans-tc";
import "taipei-sans-tc/dist/Light/TaipeiSansTCBeta-Light.css";
import "taipei-sans-tc/dist/Bold/TaipeiSansTCBeta-Bold.css";
const app = createApp(App);
app.use(router);
app.use(store);

app.component("base-card", BaseCard);
app.component("base-button", BaseButton);
app.component("base-badge", BaseBadge);
app.component("pets-filter", PetsFilter);
app.component("base-spinner", BaseSpinner);
app.component("bas-dialog", BaseDialog);

app.mount("#app");
