<template>
  <div>
<!--兩個!!是因為第一個!代表show的布林值，第二個!才為字串符的布林值-->
<base-dialog :show="!!error" title="錯誤回應" @close="handError">
<!--這裡來自最下方catch error-->
<p>{{error }}</p>
</base-dialog>
    <section>
        <base-card class="d-flex flex-column align-items-center">
        <header>
            <h2>訊息頁面</h2>
            <p class="message-page">(Message Page)</p>
        </header>
        <base-spinner v-if="isLoading"></base-spinner>
        <ul v-else-if="hasRequests &&!isLoading">
          <request-item 
          v-for="req in receivedRequest" 
          :key="req.id"
          :email="req.userEmail"
          :message="req.message"
          ></request-item>
        </ul>
        <div v-else class="d-flex flex-column align-items-center message">
          <p>你未收到任何回覆</p>
          <img src="https://media1.giphy.com/media/zitz7FTWwbDIYpm8Gn/giphy.gif?cid=ecf05e47q08rnazuodmnex8t1arsebu34kukvzpyg0923iwt&rid=giphy.gif&ct=s" width="300" alt="">
        </div>
    </base-card>
    </section>
  </div>
</template>

<script>
    import RequestItem from '../../components/requests/RequestItem.vue'
import BaseDialog from '../../components/UI/BaseDialog.vue';
import BaseSpinner from '../../components/UI/BaseSpinner.vue';
export default {
  components:{
    RequestItem,
    BaseDialog,
    BaseSpinner
},
  data(){
    return{
      isLoading:false,
      error:null,
    }
  },
  computed:{
    receivedRequest(){
      return this.$store.getters['requests/requests']
    },
    hasRequests(){
      return this.$store.getters['requests/hasRequests'];
    },
  },
  created(){
    this.loadRequest();
  },
  methods:{
    //這裡fetch get的資料
    async loadRequest(){
      this.isLoading = true
      try{
        await this.$store.dispatch('requests/fetchRequests')
      }catch(error){
        this.error = error.message ||"錯誤訊息";
      }
      this.isLoading = false;
    },
    handError(){
      this.error = null;
    }
  }
}
</script>

<style scoped>
header {
  text-align: center;
}

ul {
  list-style: none;
  margin: 2rem auto;
  padding: 0;
  max-width: 40rem;
}
h2{
  margin-top: 1.2rem;
  font-weight: 800;
  font-size: 2.3rem;
}

h3 {
  text-align: center;
}
p{
  font-size: large;
  padding-top: 1ch;
}

.message-page{
text-decoration: none;
color: rgb(41, 41, 41);
text-align: center;
font-weight: bold;
font-size:1.2rem; 
letter-spacing: 0.1rem;
margin-top: -15px;
}
</style>