<template>
    <form @submit.prevent="submitFrom">
        <div class="form-control" :class="{invalid:!email.isValid}">
            <label for="email">email：</label>
            <input 
            type="email" 
            id="email" 
            v-model.trim="email.val"
            @blur="clearValidity('email')"
            />
        <p v-if="!email.isValid">請輸入正確的email</p>
        </div>
        <div class="form-control" :class="{invalid:!message.isValid}">
        <label for="message">訊息</label>
        <textarea 
        id="message" 
        rows="5"
        v-model.trim="message.val"
        @blur="clearValidity('message')"
        ></textarea>
        <p v-if="!message.isValid">請勿輸入空白訊息</p>
        </div>
        <div class="action">
            <base-button>傳送訊息</base-button>
        </div>
    </form>
</template>

<script>
export default {
    data(){
        return{
            email:{
                val:'',
                isValid:true
            },
            message:{
                val:'',
                isValid:true
            },
            formIsValid:true
        }
    },
    methods:{
    //email includes不含@符號
        clearValidity(input){
            //這裡的input是指每個input表格
            this[input].isValid = true;
          },
          validateFrom(){
            this.formIsValid = true;
            if(this.email.val ===''||
            !this.email.val.includes('@')
            ){
            this.email.isValid = false
            this.formIsValid = false;
            }
            if(this.message.val ===''){
            this.message.isValid = false
            this.formIsValid = false;
            return;
            }
            this.$store.dispatch('requests/contactPet',{
              email:this.email,
              message:this.message,
            //這裡可以使用props來取得上父子層檔案的params
            petId:this.$route.params.id,
            })
            this.$router.replace('/pets');
        },
        submitFrom(){
            this.validateFrom();
        }
    }
}
</script>


<style scoped>

form {
  margin: 1rem;
  border: 1px solid #ccc;
  border-radius: 12px;
  padding: 1rem;
}

.form-control {
  margin: 0.5rem 0;
  border: none;
}

label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
  font-size: 1.2rem;
}

input,
textarea {
  display: block;
  width: 100%;
  border: 1px solid #9f9f9f;
  font: inherit;
  border-radius: 2rem;
  transition: all .3s linear;
  padding: 10px;
  padding-left: 1rem;
  padding-right: 1rem;
}

textarea{
    margin-bottom: 1rem;
}

input:focus,
textarea:focus {
  border-color: #888888;
  background-color: #efefef;
  outline: none;
}

.errors {
  font-weight: bold;
  color: red;
}

.actions {
  text-align: center;
}
p{
  font-size: 1rem;
  color: #e24141;
  margin-left: 10px;
  margin-top: 10px;
  font-size: 14px;
}

.invalid label {
  color: #e24141;
}

.invalid input,
.invalid textarea {
  border: 1px solid #e24141;
  border-radius: 0.5rem;
  border-radius: 2rem;
}
</style>