export default {
  async registerPet(context, data) {
    const userId = context.rootGetters.userId;
    const petData = {
      name: data.name,
      born: data.born,
      description: data.description,
      detail: data.detail,
      states: data.states,
      src: data.src,
    };

    //這裡將資料傳入firebase(POST)
    const response = await fetch(
      `https://vue-http-demo-3843c-default-rtdb.firebaseio.com/pets/${userId}.json`,
      //put的話會將已有的檔案覆蓋，未持有的上傳
      { method: "PUT", body: JSON.stringify(petData) }
    );

    // const responseData = await response.json();

    if (!response.ok) {
      //error
    }

    //上面沒加入userId是因為傳入firebase有加userId
    //但這邊是要傳入registerPet網頁中，所以需再加入一次
    context.commit("registerPet", {
      ...petData,
      id: userId,
    });
  },

  //GET獲取資料
  async loadPets(context, payload) {
    if (!payload.forceRefresh && !context.getters.shouldUpdate) {
      return;
    }
    const response = await fetch(
      //這裡不設定userId是為了獲得firebase所有資料
      `https://vue-http-demo-3843c-default-rtdb.firebaseio.com/pets.json`
    );
    const responseData = await response.json();

    if (!response.ok) {
      const error = new Error(response.message || "錯誤的Fetch");
      //使用throw可將錯誤拋出，在任何組件中(目前是PetsList)都可使用
      throw error;
    }
    //將get下來檔案(object)轉換成array

    const pets = [];

    //這裡將firebase資料與上方petData格式相同
    for (const key in responseData) {
      const pet = {
        id: key,
        name: responseData[key].name,
        born: responseData[key].born,
        description: responseData[key].description,
        detail: responseData[key].detail,
        states: responseData[key].states,
        src: responseData[key].src,
      };
      pets.push(pet);
    }
    //放入PetsList刷新頁面
    context.commit("setPets", pets);
    context.commit("setFetchTimestamp");
  },
};
