<template>
    <div>
    <section>  
    <base-card>
    <div class="imageCard">
        <img :src="src" class="petImage figure-img img-fluid rounded" width="600">
    </div>  
        <h2>{{ name }}</h2>
        <h3>出生日期:{{ born }}</h3>
    </base-card>
    </section>
    <section>
        <base-card>
        <header>
        <h4>聯繫請點擊下方按鈕</h4>
        <div class="d-flex justify-content-center">
                <base-button 
                link 
                :to="petsContactLink">
                聯繫方式</base-button>    
        </div>
        </header>
        <router-view v-slot="slotProps">
            <transition name="button" mode="out-in">
                <component :is="slotProps.Component"></component>
            </transition>
        </router-view>
    </base-card>
    </section>
    <section>
        <div class="base-card">
        <base-badge class="mt-2 mb-4" v-for="states in states" 
            :key="states" 
            :type="states" 
            :title="states"
            ></base-badge>
        <p>{{description}}</p>
        <p>{{detail}}</p>
    </div>
    </section>
</div>
</template>

<script>
export default {
    props: ['id'],
    data(){
        return{
            selectedPet:null,
            dialogIsVisible: false ,
        }
    },
    computed:{
    petsContactLink(){
        return this.$route.path + '/contact'
        },
        name(){
        return this.selectedPet.name
    },
    born(){
        return this.selectedPet.born
    },
    states(){
        return this.selectedPet.states
    },
    description(){
        return this.selectedPet.description
    },
    detail(){
        return this.selectedPet.detail
    },
    src(){
        return this.selectedPet.src
    }
    },
    created(){
       this.selectedPet = this.$store.getters['pets/pets'].find((pet) => pet.id === this.id);
    }
}
</script>

<style scoped>
.imageCard{
    margin: 1rem;
}
.base-card{
    border-radius: 12px;
    padding: 1.5rem;
    margin: 2rem auto;
    max-width: 35rem;
    border: none;
    background-color: rgb(255, 255, 255);
}
h2{
    margin-top: 20px;
    font-size: 2.2rem;
    font-weight: bold;
    text-align: center;
}
h3{
    margin-top: 7px;
  font-size: 1rem;
  font-weight: bold;
  text-align: center;
  color: #6b6d72;
}
h4{
    margin-top: 20px;
    margin-bottom: 10px;
    font-size: 1.5rem;
    font-weight: bold;
    text-align: center;
}
.button-enter-from{
  opacity: 0;
  transform: translateX(-30px);
}
.button-leave-to{
  opacity: 0;
  transform: translateX(30px);
}
.button-enter-active{
  transition: all 0.3s ease-out;
}
.button-leave-active{
  transition: all 0.3s ease-in;
}

.button-enter-to,
.button-leave-from{
  opacity: 1;
  transform: translateY(0);
}



</style>