import { createStore } from "vuex";

import petsModule from "./modules/pets/index.js";
import requestsModule from "./modules/requests/index.js";

const store = createStore({
  modules: {
    pets: petsModule,
    requests: requestsModule,
  },
  state() {
    return {
      userId: "c4",
    };
  },
  getters: {
    userId(state) {
      return state.userId;
    },
  },
});

export default store;
