<template>
    <form @submit.prevent="submitFrom">
        <div class="form-control" :class="{invalid:!name.isValid}">
        <label for="name" >姓名：</label>
        <input
        type="text"
        id="name" 
        v-model.trim="name.val"
        @blur="clearValidity('name')" 
        placeholder="例如:王小明"
        />
        <p v-if="!name.isValid">名字不能為空白</p>
        </div>
        <div class="form-control" :class="{invalid:!born.isValid}">
        <label for="born">出生日期：</label>
        <input 
        type="text" 
        id="born" 
        v-model.trim="born.val" 
        @blur="clearValidity('born')"
        placeholder="例如：2010年9月9日"
        >
        <p v-if="!born.isValid">出生日期不能為空白</p>
        </div>
        <div class="form-control" :class="{invalid:!description.isValid}">
        <label for="description">敘述：</label>
        <textarea 
        name="description" 
        rows="5" 
        v-model.trim="description.val"
        placeholder="例如：在哪裡撿到..."
        @blur="clearValidity('description')">
      </textarea>
        <p v-if="!description.isValid">敘述不能為空白</p>
        </div>
        <div class="form-control" :class="{invalid:!detail.isValid}" >
        <label for="detail">領養條件：</label>
        <textarea
        disabled 
        name="detail" 
        rows="9" 
        v-model.trim="detail.val" 
        @blur="clearValidity('detail')">
        </textarea>
        <p v-if="!detail.isValid">領養條件不能為空白</p>
        </div>
        <div class="form-control" :class="{invalid:!states.isValid}">
            <h3>篩選條件：</h3>
            <div>
                <input 
                type="checkbox" 
                id="男生" 
                value="男生" 
                v-model="states.val"
                @blur="clearValidity('states')">
                
                <label for="男生">男生</label>
            </div>
            <div>
                <input 
                type="checkbox" 
                id="女生" 
                value="女生" 
                v-model="states.val"
                @blur="clearValidity('states')"
                >
                <label for="女生">女生</label>
            </div>
            <div>
                <input 
                type="checkbox" 
                id="未結紮"
                value="未結紮" 
                v-model="states.val"
                @blur="clearValidity('states')"
                >
                <label for="未結紮">未結紮</label>
            </div>
            <div>
                <input 
                type="checkbox"
                id="結紮" 
                value="結紮" 
                v-model="states.val"

                @blur="clearValidity('states')"
                >
                <label for="結紮">結紮</label>
            </div>
            <div>
                <input 
                type="checkbox" 
                id="狗狗" 
                value="狗狗"
                v-model="states.val"
                @blur="clearValidity('states')"
                >
                <label for="狗狗">狗狗</label>
            </div>
            <div>
                <input 
                type="checkbox" 
                id="貓貓" 
                value="貓貓" 
                v-model="states.val"
                @blur="clearValidity('states')"
                >
                <label for="貓貓">貓貓</label>
            </div>
            <p v-if="!states.isValid">請至少選擇一項篩選條件</p>
        <div class="img" :class="{invalid:!src.isValid}">
        <label for="src">照片：</label>
        <textarea 
        placeholder="例如：http://..."
        name="src" 
        rows="2" 
        v-model.trim="src.val"
        @blur="clearValidity('src')">
      </textarea>
        <p v-if="!src.isValid">照片不能為空白</p>
        </div>
        <p v-if="!fromIsValid">請修復上面錯誤並再次執行</p>
        </div>
        <base-button class="mx-3">確認</base-button>
    </form>
</template>

<script>
    export default{
      emits:['save-data'],
        data(){
            return{
                name:{
                  val:'',
                  isValid:true
                },
                born:{
                  val:'',
                  isValid:true
                },
                description:{
                  val:'',
                  isValid:true
                },
                detail:{
                val:'領養條件：\n您好，感謝您願意給浪浪機會但為了避免棄養退養發生，領養有以下《基本條件規定》：\n1.必須年滿25歲\n2.有穩定工作\n3.住雅/套房無法領養狗\n4.居住成員全數贊成養狗貓\n5.不接受放養或關籠飼養\n6.一定要結紮',
                isValid:true
                },
                states:{
                  val:[],
                  isValid:true
                },
                src:{
                  val:'',
                  isValid:true
                },
              fromIsValid:true
            }
        },
        methods:{
          //清除clearValidity驗證失敗 使用@blur
          clearValidity(input){
            //這裡的input是指每個input表格
            this[input].isValid = true;
          },
          validateFrom(){
            this.fromIsValid = true;
            if(this.name.val === ''){
              this.name.isValid = false
              this.fromIsValid = false
            }
            if(this.born.val === ''){
              this.born.isValid = false
              this.fromIsValid = false
            }
            if(this.description.val === ''){
              this.description.isValid = false
              this.fromIsValid = false
            }
            if(this.detail.val === ''){
              this.detail.isValid = false
              this.fromIsValid = false
            }
            if(this.states.val.length === 0){
              this.states.isValid = false
              this.fromIsValid = false
            }
            if(this.src.val === ''){
              this.src.isValid = false
              this.fromIsValid = false
            }

          },
            submitFrom(){
              this.validateFrom();
              //設定fromIsValid=false就不執行
              if(!this.fromIsValid){
                return;
              }

                const formData = {
                    name: this.name.val,
                    born:this.born.val,
                    description:this.description.val,
                    detail:this.detail.val,
                    states:this.states.val,
                    src:this.src.val
                };
                this.$emit("save-data",formData)
            },
        }
    }
</script>

<style scoped>

.form-control {
  margin: 0.5rem 0;
  outline: none;
  border: none;
}

*{
    font-size: 1.3rem;
}

label {
  font-weight: bold;
  display: block;
  margin-bottom: 0.5rem;
  margin-left: 0.5rem;
  color: #434343;
}

input[type='checkbox'] + label {
  font-weight: normal;
  display: inline;
  margin: 0 0 0 0.5rem;
  outline: none;
}

input,
textarea {
  display: block;
  width: 100%;
  border: 1px solid #9f9f9f;
  font: inherit;
  border-radius: 2rem;
  transition: all .3s linear;
  padding: 10px;
  padding-left: 1rem;
  padding-right: 1rem;
}


input:focus,
textarea:focus {
  background-color: white;
  outline: none;
  box-shadow:4px 3px 10px 1px #d4d4d4;
}


input[type="checkbox"] {
  display: inline-block;
  width: auto;
}


h3 {
  font-weight: bold;
  display: block;
  margin-bottom: 0.5rem;
}

.invalid label {
  color: #ec2c2c;
}

.invalid input,
.invalid textarea {
  border: 1px solid #ff0101;
  border-radius: 0.5rem;
  border-radius: 2rem;
}
p{
  font-size: 1rem;
  color: #939393;
  margin-left: 10px;
  margin-top: 5px;
  font-size: 14px;
}

.img{
  margin-top:1rem;
  margin-bottom: 1rem;
  font-size: 16px;
}

</style>